:root {
  --font-smono: "Space Mono", monospace;
}

:root {
  --strong-cyan: hsl(172, 67%, 45%);
  --dark-cyan: hsl(183, 100%, 15%);
  --dg-cyan-1: hsl(186, 14%, 43%);
  --dg-cyan-2: hsl(184, 14%, 56%);
  --lg-cyan-1: hsl(185, 41%, 84%);
  --lg-cyan-2: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
  --reset-button: hsl(183, 61%, 27%);
}

html {
  font-size: rem(24);
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-smono);
  background: var(--lg-cyan-1);
  min-height: 100vh;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 1200px) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 1200px) {
  .container {
    padding: 0;
  }
}

.header {
  margin: auto;
  padding: 2.8125rem 0 2.8125rem 0;
}
.header img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 840px) {
  .header {
    padding: 6.25rem 0 3.4375rem 0;
  }
}
@media (min-width: 1200px) {
  .header {
    padding: 9.375rem 0 5rem 0;
  }
}

.calculator {
  background-color: var(--white);
  border-radius: 1.25rem;
  display: flex;
  flex-direction: column;
  padding: 1.25rem;
}
@media (min-width: 640px) {
  .calculator {
    margin: auto;
    width: 80%;
  }
}
@media (min-width: 840px) {
  .calculator {
    flex-direction: row;
    padding: 1.5625rem;
  }
}
@media (min-width: 1200px) {
  .calculator {
    width: 60%;
  }
}

.input {
  width: 100%;
  padding: 0.9375rem 0.9375rem 0.9375rem 0.9375rem;
}
@media (min-width: 1200px) {
  .input {
    width: 50%;
    padding: 0.9375rem 2.5rem 0.9375rem 0.9375rem;
  }
}
.input__label {
  font-size: 0.9375rem;
  font-weight: 700;
  color: var(--dg-cyan-1);
  padding-bottom: 0.625rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.input__label .alert {
  display: none;
}
.input__label .trigger-warning {
  display: block;
  color: chocolate;
}
.input__number .svg-icon {
  position: absolute;
  padding: 0.875rem 0 0 1.125rem;
}
.input__radio {
  display: none;
}

.input-box {
  background-color: var(--lg-cyan-2);
  border: 2px solid var(--lg-cyan-2);
  border-radius: 0.3125rem;
  padding: 0.25rem 1.125rem;
  font-size: 1.4375rem;
  font-weight: 700;
  color: var(--dark-cyan);
  font-family: var(--font-smono);
  cursor: pointer;
  width: 100%;
  text-align: right;
}
.input-box::placeholder {
  color: var(--dg-cyan-2);
}
.input-box:focus {
  outline-color: var(--strong-cyan);
}
.input-box.warning {
  border-color: chocolate;
}
.input-box__custom {
  padding: 0.25rem 0;
}
.input-box__custom::placeholder {
  color: var(--dg-cyan-1);
  text-align: center;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.bill {
  padding-bottom: 2.5rem;
}

.tip-selector {
  padding-bottom: 2.5rem;
}
.tip-selector__buttons {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 50fr 50fr;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .tip-selector__buttons {
    grid-template-columns: 33.3fr 33.3fr 33.3fr;
  }
}
.tip-selector__pt {
  border-radius: 0.3125rem;
  padding: 0.375rem 0;
  color: var(--white);
  background-color: var(--dark-cyan);
  font-size: 1.4375rem;
  text-align: center;
  cursor: pointer;
}
.tip-selector__pt:hover {
  background-color: var(--strong-cyan);
  color: var(--dark-cyan);
  font-weight: 700;
}
.tip-selector__pt.active {
  background-color: var(--strong-cyan);
  color: var(--dark-cyan);
  font-weight: 700;
}
.tip-selector__custom {
  border-radius: 0.3125rem;
  padding: 0.375rem 0;
  color: var(--dg-cyan-2);
  background-color: var(--lg-cyan-2);
  font-size: 1.4375rem;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
}
.tip-selector__custom:hover {
  background-color: var(--strong-cyan);
  color: var(--dark-cyan);
  font-weight: 700;
}

.screen {
  width: 100%;
  background-color: var(--dark-cyan);
  padding: 3.125rem 2.1875rem 2.1875rem 2.1875rem;
  border-radius: 0.625rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 1200px) {
  .screen {
    width: 50%;
  }
}

.screen-grid {
  display: grid;
  grid-template-rows: 1.5625rem;
  line-height: 1;
  padding-bottom: 3.125rem;
}
.screen-grid__labeltt {
  grid-column: 1;
  grid-row: 1;
  color: var(--white);
  font-size: 0.9375rem;
}
.screen-grid__labelst {
  grid-column: 1;
  grid-row: 2;
  color: var(--dg-cyan-2);
  font-size: 0.75rem;
}
.screen-grid__amount {
  grid-column: 2;
  grid-row: 1/span 2;
  text-align: right;
  line-height: 0.8;
  color: var(--strong-cyan);
  font-size: 2.8125rem;
  display: flex;
  justify-content: flex-end;
}
.reset-button {
  border-radius: 0.3125rem;
  padding: 0.375rem 0;
  color: var(--dark-cyan);
  background-color: var(--reset-button);
  font-size: 1.4375rem;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
}
.reset-button:hover {
  background-color: var(--strong-cyan);
  color: var(--dark-cyan);
  font-weight: 700;
}